import Cartesian2 from "../../../Core/Cartesian2.js";
import Matrix4 from "../../../Core/Matrix4.js";
import defaultValue from "../../../Core/defaultValue.js";
import Cartesian3 from "../../../Core/Cartesian3.js";
import AxisAlignedBoundingBox from "../../../Core/AxisAlignedBoundingBox.js";
import PolygonPipeline from "../../../Core/PolygonPipeline.js";
import WindingOrder from "../../../Core/WindingOrder.js";

class BorderInfo {
    /**
     * create a BorderInfo
     */
    constructor() {
        this._pt2DAry = [];
        this._under = 0;
        this._height = 0;
    }

     /**
     * clone
     * @param {BorderInfo} borderInfo
     * @returns {BorderInfo}
     */
    static clone(borderInfo) {
        const under = borderInfo._under;
        const height = borderInfo._height;

        const pt2DAry = [];
        borderInfo._pt2DAry.forEach((pt2D) => {
            pt2DAry.push(new Cartesian2(pt2D.x, pt2D.y));
        });

        const retBorderInfo = new BorderInfo();
        retBorderInfo._under = under;
        retBorderInfo._height = height;
        retBorderInfo._pt2DAry = pt2DAry;
        return retBorderInfo;
    }

    /**
     * get abb box
     * @returns {AxisAlignedBoundingBox}
     */
    getBoundingbox() {
        const cornAry = [];
        this.getPtAry(true, cornAry);
        this.getPtAry(false, cornAry);
        return AxisAlignedBoundingBox.fromPoints(cornAry);
    }

    /**
     * get triangles
     * @param {Cartesian3[]} ptAry pt out Ary
     * @param {Number[]} indexAry indice out Ary
     */
    createTriangle(ptAry, indexAry) {
        let startIndex = ptAry.length;
        this.getPtAry(true, ptAry);
        let colLen = ptAry.length - startIndex;
        this.getPtAry(false, ptAry);

        let nextIndex = 0;
        for (let index = 0; index < colLen; index++) {
            if (index === colLen - 1) {
                nextIndex = 0;
            } else {
                nextIndex = index + 1;
            }

            indexAry.push(startIndex + index);
            indexAry.push(startIndex + nextIndex);
            indexAry.push(startIndex + colLen + nextIndex);

            indexAry.push(startIndex + index);
            indexAry.push(startIndex + colLen + nextIndex);
            indexAry.push(startIndex + colLen + index);
        }

        const orIndexAry = PolygonPipeline.triangulate(this._pt2DAry);
        indexAry.push(...orIndexAry.map((index) => { return startIndex + colLen + index; }));
        indexAry.push(...orIndexAry.reverse().map((index) => { return startIndex + index; }));
    }

    /**
     * @type {Cartesian2[]}
     */
    set pt2DAry(value) {
        this._pt2DAry = defaultValue(value, []);
        if (this._pt2DAry.length > 2) {
            if (PolygonPipeline.computeWindingOrder2D(this._pt2DAry) === WindingOrder.CLOCKWISE) {
                this._pt2DAry.reverse();
            }
        }
    }

    get pt2DAry() {
        return this._pt2DAry;
    }

    /**
     * @type {Number}
     */
    set under(value) {
        this._under = value;
    }

    get under() {
        return this._under;
    }

    /**
     * @type {Number}
     */
    set height(value) {
        this._height = value;
    }

    get height() {
        return this._height;
    }

    /**
     * get pt ary for 3d
     * @param {Boolean} bUnder is under pt
     * @param {Cartesian3[]} ptAry pt out Ary
     */
    getPtAry(bUnder, ptAry) {
        this._pt2DAry.forEach(pt2D => {
            let pt3D = new Cartesian3(pt2D.x, pt2D.y, this._under);
            if (!bUnder) {
                pt3D.z += this._height;
            }
            ptAry.push(pt3D);
        });
    }

    /**
     * trans matrix
     * @param {Matrix4} matrix
     */
    transformBy(matrix) {
        let dMin = 1000000000;
        for (let index = 0; index < this._pt2DAry.length; index++) {
            let pt2D = this._pt2DAry[index];
            let pt3D = new Cartesian3(pt2D.x, pt2D.y, this._under);
            Matrix4.multiplyByPoint(matrix, pt3D, pt3D);
            pt2D.x = pt3D.x;
            pt2D.y = pt3D.y;
            if (pt3D.z < dMin) {
                dMin = pt3D.z;
            }
        }
        if (dMin < 1000000000) {
            this._under = dMin;
        }
        else {
            this._under = 0;
        }
    }
}

export default BorderInfo;
